    <el-dialog title="分配转诊医生" :visible.sync="shiftDocVisible" width="30%">
      <div class="cwt">选择医生</div>
      <el-select v-model="value" placeholder="请选择">
        <el-option v-for="item in docs" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="docConfirm">确认</el-button>
      </span>
    </el-dialog>

    <el-dialog title="取消转诊原因" :visible.sync="cancelVisible" width="30%">
      <el-input type="textarea" :rows="4" placeholder="请输入内容" v-model="cancelReason"></el-input>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="cancelConfirm">提交</el-button>
      </span>
    </el-dialog>
  </el-container>
</template>
<style scoped>
.info_top {
  text-align: left;
}
.dialog_patientinfo {
  display: flex;
  background: #efeff4;
  padding: 10px 20px;
  align-items: center;
  font-size: 14px;
  color: #666;
}
/* .main{
    background: #F2F6FF;

} */
.left-area {
  background: white;
  margin: 20px;
}
.right-area {
  /* padding: 0 20px; */
  margin: 20px;
  min-width: 600px;
}
.el-main {
  padding: 0px;
}
.search-input input {
  background-color: #e5e5e5;
}
.el-input-group__append {
  background-color: rgb(39, 113, 217);
  border: 1px solid rgb(39, 113, 217);
}
.el-icon-search {
  color: #fff;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.item {
  display: flex;
  width: 100%;
  padding: 10px 15px;
  justify-items: center;
  box-sizing: border-box;
  border-bottom: 1px solid #e5e5e5;
  height: 80px;
}
.header-img {
  width: 60px;
  height: 60px;
  margin-right: 10px;
}
.info {
  line-height: 30px;
  text-align: left;
}
.info span {
  margin-right: 5px;
  color: #333;
}
.name {
  font-size: 18px;
}
.phone {
  color: rgb(39, 113, 217);
}
.selected {
  background-color: #e5e5e5;
}
.condition {
  display: flex;
  flex-wrap: wrap;
}
.condition > div {
  width: 50%;
  text-align: left;
  padding: 0 10px;
  box-sizing: border-box;
  margin-bottom: 20px;
}
.r-info {
  border-left: 5px solid rgb(39, 113, 217);
  padding: 0 5px;
  margin-bottom: 5px;
}
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 100%;
}
.el-select {
  width: 100%;
}
.room-div {
  background-color: #fff;
  margin: 0px 20px 0px 0px;
  padding: 3px 20px 20px 20px;
}
.room {
  display: flex;
}
.room-item {
  background-color: #edf4f9;
  padding: 8px;
}
.room-item img {
  width: 20px;
}
.room-item div {
  font-size: 12px;
}
.states {
  text-align: center;
  height: 35px;
  line-height: 35px;
}
.states span {
  margin-right: 10px;
}
.states i {
  width: 12px;
  border-radius: 15px;
  display: inline-block;
  height: 12px;
  margin-right: 5px;
}
.unse {
  background-color: #e5e5e5;
}
.manse {
  background-color: #4aff40;
}
.womense {
  background-color: #ffd640;
}
.alse {
  background-color: rgb(39, 113, 217);
}
.submit-btn {
  width: 300px;
  margin-top: 20px;
}
element.style {
  transform: translateX(0px);
}
.cwt {
  text-align: left;
  margin: 10px 5px;
  padding-left: 10px;
  border-left: 3px solid #488aff;
  font-size: 16px;
  font-weight: bold;
}
.shift_advice {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 14px;
  margin-left: 5px;
  margin-right: 20px;
  padding-left: 15px;
  line-height: 35px;
  background: white;
}
.shift_advice .left {
  text-align: left;
  display: inline-block;
  width: 100px;
  font-weight: bold;
}
</style>
<script>
export default {
  data() {
    return {
        cancelReason:"",
      cancelVisible: false,
      docs: [
        {
          value: "选项1",
          label: "张医生"
        },
        {
          value: "选项2",
          label: "李医生"
        },
        {
          value: "选项3",
          label: "王医生"
        },
        {
          value: "选项4",
          label: "赵医生"
        }
      ],
      shiftDocVisible: false,
      centerDialogVisible: false,
      activeName: "first",
      options: [
        {
          value: "YS",
          label: "上海杨思医院"
        },
        {
          value: "AD",
          label: "上海安达医院"
        }
      ],
      value: "",
      value1: "",
      value2: "",
      value3: "",
      index: 0,
      bedurl: "../../assets/hui.png",
      albedurl: "../../assets/lan.png",
      bedIndex: 0
    };
  },
  methods: {
    selectPatient: function(name, index) {
      this.value2 = name;
      this.index = index;
    },
    selectBed: function(index) {
      this.bedIndex = index;
    },
    open: function() {
      //   this.$message({ message: "提交成功", center: true, type: "success" });
    },
    handleClick(tab, event) {
      console.log(tab, event);
    },
    confirmShift: function() {
      this.centerDialogVisible = false;
      this.shiftDocVisible = true;
    },
    cancelShift: function() {
      this.centerDialogVisible = false;
      this.cancelVisible = true;
    },
    docConfirm: function() {
      this.shiftDocVisible = false;
       this.$message({
          message: "提交成功",
          center: true
        });
    },
    cancelConfirm: function() {
      this.cancelVisible = false;
       this.$message({
          message: "提交成功",
          center: true
        });
    }
  }
};
</script>


